/*  БАЗОВЫЕ НАСТРОЙКИ  */
.reset();
html{
  font-size: 14px;

  .lg-min({
    font-size: 20px;
  });
}
body{
  font-family: var(--font-averta);
  line-height: 1.3;
  font-size: 4.44vw;
  font-weight: 400;
  font-style: normal;
  background: #fff;
  color: #000;

  .lg-min({
    font-size: .83vw;
  });
}
*{
  box-sizing: border-box;
  max-height: 100000px;
}
a{
  color: inherit;
  outline: none;
  text-decoration: none;
}
button, [role="button"]{
  cursor: pointer;
}
strong{
	font-weight: 700;
}


.main{
  min-width: 360px;
}
.container{
  position: relative;
  z-index: 1;
  margin: 0 4.44vw;

  .lg-min({
    max-width: 87.5vw;
    margin: 0 auto;
  });
}
.text-center{
  text-align: center;
}
.d-ib{
  display: inline-block;
}
[hidden], .hidden{
  display: none !important;
}

.display-flex{
  display: flex;
  flex-wrap: wrap;
}
.justify-center{
  justify-content: center;
}
.align-center{
  align-items: center;
}
.self-center{
  align-self: center;
}

.mobile-hidden{
  .lg({
    display: none !important;
  });
}
.desktop-hidden{
  .lg-min({
    display: none !important;
  });
}

