.logo{
  display: inline-block;
}
.phone{
  white-space: nowrap;
}




.search{
  position: relative;
  display: flex;
  background: #F6F6F7;
  border-radius: 2000/360vw;
  height: 4000/360vw;

  .lg-min({
    height: 5000/1920vw;
    border-radius: 1.25vw;
    padding-left: 0.5vw;
  });

  &__submit{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 4000/360vw;
    height: 4000/360vw;
    margin-right: 0.2em;
    padding: 0;
    border: 0;
    background: none;
    outline: none;
    cursor: pointer;

    .lg-min({
      width: 2.6vw;
      height: 2.6vw;
    });

    svg{
      display: block;
      width: 2400/50%;
    }
  }
  &__input{
    display: block;
    flex: 1;
    background: none;
    border: 0;
    outline: none;
    font-family: var(--font-averta);
    font-weight: 400;
    color: #000;
    padding: .1em 1em .2em .2em;
    font-size: 1400/360vw;

    .lg-min({
      font-size: 0.94vw;
    });

    &::placeholder{
      color: #9CA5AC;
    }
  }
}


.close{
  display: block;
  position: relative;
  padding: 5px;
  background: none;
  border: 0px;
  outline: none;
  cursor: pointer;
  transition: .3s color;
  color: green;

  svg, img{
    width: 1rem;
    height: 1rem;
    fill: currentColor;
    user-select: none;
    pointer-events: none;
  }
}
