.slider{

	&__nav{
		max-width: 12000/360vw;
		margin: 2400/360vw auto 0;
		position: relative;
		display: flex;
		justify-content: space-between;
	}
	&__arrow{
		display: block;
		width: 5000/360vw;
		background: rgba(26, 201, 84, 0.20);
		border-radius: 800/360vw;
		color: var(--color-main);
		transition: 0.3s color, 0.3s background;

		&:not(.swiper-button-disabled):hover{
			background: var(--color-main);
			color: #fff;
		}
		&.swiper-button-disabled{
			opacity: 0.5;
			cursor: default;
		}

		svg{
			stroke: currentColor;
		}
	}
	&__arrow_prev{

	}
	&__arrow_next{

	}
	&__info{
		
	}
	&__curr{

	}
	&__count{

	}
}


@media (min-width: @w-lg) {
	.slider{

		&__nav{
			margin-top: 2.08vw;
		}
		&__arrow{
			width: 5000/1920vw;
			border-radius: 800/1920vw;
		}
	}
}
