.section{
  position: relative;
  overflow: hidden;
  padding: 3rem 0;

  &__title{
    position: relative;
    display: block;
    margin: 0;
    font-weight: 700;
    font-size: 6.5vw;
    line-height: 64/58;
    padding-left: 6vw;

    &::before{
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0%;
      transform: translate(-75%, 0%);
      width: 20vw;
      height: 18vw;
      background: url(../img/title.png) center / contain no-repeat;
    }

    strong{
      color: var(--color-main);
    }
  }
}


@media (min-width: @w-xxs) {
	.section{}
}


@media (min-width: @w-xs) {
	.section{}
}


@media (min-width: @w-sm) {
	.section{}
}


@media (min-width: @w-md) {
	.section{}
}


@media (min-width: @w-lg) {
	.section{
    padding: 80px 0 80px;
  }
}
