.top{
  padding: 0 0 6vw;

  &__wrap{
    margin-top: 3vw;
    position: relative;
    background: #D1F4D8;
    border-radius: 6vw;
    padding: 2000/360vw;

    &&-1{
      padding-bottom: 32vw;
    }
  }
  &__desc{
    position: relative;
    z-index: 1;
  }
  &__title{
  	display: block;
    font-weight: 700;
    font-size: 2800/360vw;
    line-height: 1.21;
  }
  &__label{
    display: flex;
    align-items: center;
    width: max-content;
    margin-left: auto;
    margin-bottom:7vw;
    background: #fff;
    border-radius: 10vw;
    padding: 2.7vw 6.8vw;
    font-size: 1000/360vw;
    line-height: 1.2;

    span{
      display: block;
      font-weight: 800;
      font-size: 2200/360vw;
      color: var(--color-main);
      margin-right: 1000/360vw;
      line-height: 1;
    }
  }
  &__pic-1{
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;

    .img-1{
      position: absolute;
      z-index: 0;
      left: 0;
      bottom: 0;
      width: 100%;
      object-fit: contain;
      border-radius: 6vw;
      overflow: hidden;
    }
    .img-2{
      display: none;
    }
  }
  &__btns{
    margin-top: 7vw;
  }
  &__dots.swiper-pagination-bullets.swiper-pagination-horizontal{
    display: none;
  }
  &__slider{
    position: relative;
    margin-top: 3000/360vw;
  }
  &__slide{
    display: block;
    position: relative;
    border-radius: 2000/360vw;
    background: #EDF3F5;
    overflow: hidden;

    &-pic{
      position: absolute;
      z-index: 0;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;

      img{
        .positionFull();
        object-fit: cover;
      }
    }
    &-desc{
      position: relative;
      z-index: 1;
      padding: 5.2vw 30vw 2000/360vw 2000/360vw;
    }
    &-title{
      display: block;
      margin-bottom: 2.6vw;
      line-height: 1.15;
      height: 1.15*2em;
      font-weight: 700;
      font-size: 2200/360vw;
    }
    &-txt{
      margin-bottom: 1800/360vw;
      display: block;
      line-height: 1.5;
      font-size: 1200/360vw;
      color: #8B8B8B;
    }
    &-link{
      display: block;
      width: 5000/360vw;
      background: var(--color-main);
      color: #fff;
      box-shadow: 8px 8px 16px rgba(0, 151, 50, 0.24);
      border-radius: 50%;

      svg{
        fill: currentColor;
      }
    }
  }
}


@media (min-width: @w-lg) {
  .top{

    &__wrap{
      margin-top: .9vw;
      padding: 2.2vw 0 2vw 8200/1920vw;
      border-radius: 7000/1920vw;
      min-height: 44000/1920vw;

      &&-1{
        padding-bottom: 2vw;
      }
    }
    &__desc{
      width: 45%;
    }
    &__title{
      font-size: 5400/1920vw;
    }
    &__btns{
      margin-top: 2.2vw;
    }
    &__pic-1{

      .img-1{
        border-radius: 7000/1920vw;
        height: 100%;
        object-fit: cover;
      }
      .img-2{
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        width: 113000/1680%;
        margin-right: -6%;

        &::after{
          content: '';
          display: block;
          position: absolute;
          z-index: 0;
          right: 0;
          bottom: 0;
          width: 21400/1130%;
          padding-bottom: 20500/1130%;
          margin-bottom: -4.7%;
          margin-right: 3.1%;
          background: url(../img/top/pic-1-3.png) center / contain no-repeat;
        }
        img{
          position: relative;
          z-index: 1;
        }
      }
    }
    &__label{
      margin: 0;
      position: absolute;
      z-index: 1;
      top: 4600/1920vw;
      right: 7200/1920vw;
      width: 40800/1920vw;
      font-size: 0.83vw;
      padding: 0.8vw 1.7vw;

      span{
        margin-right: 1500/1920vw;
        font-size: 3400/1920vw;
      }
    }
    &__dots.swiper-pagination-bullets.swiper-pagination-horizontal{
      display: flex;
      gap: 1.15vw;
      width: 55400/1920vw;
      position: absolute;
      bottom: 1.56vw;
      left: 50%;
      margin-left: -55400/1920vw/2;

      .swiper-pagination-bullet{
        display: block;
        height: 4px;
        flex: 1;
        border-radius: 2px;
        background: #fff;
        opacity: 0.5;

        &.swiper-pagination-bullet-active{
          opacity: 1;
        }
      }
    }

    &__slider{
      margin-top: 2.08vw;
      padding: 0 2.66vw;

      .slider__nav{
        margin: 0;
        position: static;
        display: block;
      }
      .slider__arrow{
        position: absolute;
        z-index: 10;
        top: 50%;
        transform: translate(0%, -50%);
      }
      .slider__arrow_prev{
        left: -1vw;
      }
      .slider__arrow_next{
        right: -1vw;
      }

      .swiper-slide{
        padding: 0 .94vw;
      }
    }
    &__slide{
      border-radius: 2.08vw;

      &-desc{
        padding: 1.3vw 2.1vw 1.3vw;
      }
      &-title{
        margin-bottom: .5vw;
        font-size: 3200/1920vw;
      }
      &-txt{
        margin-bottom: 1vw;
        font-size: 0.83vw;
      }
      &-link{
        width: 5000/1920vw;
      }
    }
  }
}
