.aside{
  display: none;
  position: fixed;
  z-index: 110;
  left: 0;
  top: 0;
  width: 300px;
  height: 100%;
  transform: translateX(-120%);
  opacity: 0;
  transition: .5s;
  background: #fff;
  overflow: auto;

  &.active{
    transform: translateX(0);
    opacity: 1;

    &+.aside__overlay{
      display: block;
    }
  }

  &+.aside__overlay{
    position: fixed;
    z-index: 109;
    display: none;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.3);
  }

  &__wrap{
    position: relative;
    height: 100%;
    padding: 60px 15px 30px;
  }
  &__close{
    position: absolute;
    top: 20px;
    right: 20px;
  }
}


