.header{
  padding: 4.44vw 0;
  
  &__logo{
    width: 6800/360vw;
    flex: 0 0 6800/360vw;
    margin-right: 4.44vw;
  }
  &__wrap{
    flex: 1;
  }
  &__cat{
    margin-right: 900/360vw;
  }
  &__zoom,
  &__soc-1{
    margin-right: 900/360vw;
  }
  &__row-1{
    margin-bottom: 4.44vw;
  }
  &__row-2,
  &__row-1{
    flex: 1 1 100%;
  }
  &__phones{
    margin-right: auto;
  }

  &__nav,
  &__search,
  &__weight,
  &__cart,
  &__favorite,
  &__profile{
    display: none;
  }
}



@media (min-width: @w-lg) {
  .header{
    padding: 1.4vw 0;

    &__logo{
      width: 12000/1920vw;
      flex-basis: 12000/1920vw;
      margin-right: 4500/1920vw;
      // padding-top: 0.4vw;
    }
    &__wrap{
      display: flex;
      flex-wrap: wrap;
    }
    &__row-1{
      order: 2;
      margin-bottom: 0;
      margin-top: 1vw;
    }
    &__row-2{
      order: 1;
    }
    &__phones{
      margin-right: 1.35vw;
    }
    &__soc-1{
      margin-right: 800/1920vw;
    }

    &__nav{
      display: block;
      margin-left: 5.2vw;
      flex: 1;
    }

    &__zoom,
    &__burger{
      display: none;
    }
    &__search{
      display: block;
      width: 66800/1920vw;
      margin-right: 4200/1920vw;
    }
    &__cat{
      margin-right: 1.56vw;
    }
    &__favorite{
      display: block;
      margin-right: 0.73vw;
    }
    &__weight{
      display: block;
    }
    &__cart{
      display: block;
      margin-left: auto;
    }
    &__profile{
      display: block;
      margin-left: .83vw;
    }
  }
}