.menu{
	&__item{
		margin-bottom: 15px;
	}
	&__link{
		display: inline-block;
		transition: 0.3s color;
		
	}
}


@media (min-width: @w-lg) {
	.menu{
		display: flex;
		justify-content: flex-end;
		margin: 0 -1.3vw;
		color: #9CA5AC;
		font-size: 0.83vw;

		&__item{
			margin: 0 1.3vw;
		}
		&__link{

			&:hover{
				color: var(--color-main);
				font-weight: 600;
			}
		}

	}
}