.btn1{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 28800/360vw;
  height: 6400/360vw;
  padding: .2em 1em .2em;
  border: none;
  border-radius: 1300/360vw;
  cursor: pointer;
  outline: none;
  background: var(--color-main);
  text-align: center;
  text-decoration: none;
  color: #fff;
  font-family: var(--font-averta);
  font-weight: 700;
  font-size: 1800/360vw;
  transition-duration: .4s;
  transition-property: background;
  box-shadow: 1.3vw 1.3vw 2.6vw rgba(0, 0, 0, 0.15);

  .lg-min({
    min-width: 44300/1920vw;
    height: 9700/1920vw;
    border-radius: 1.04vw;
    font-size: 2800/1920vw;
  });

  &:hover{
    background: var(--color-2);
    text-decoration: none;

    .btn1__arr{
      fill: #C08B22;
    }
  }

  &__arr{
    width: 40/28em;
    display: block;
    margin-left: 44/28em;
    fill: #A9EFC0;
    transition: 0.3s fill;
  }
}

.circle-btn{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 4000/360vw;
  height: 4000/360vw;
  border-radius: 50%;
  background: #fff;
  border: 0;
  cursor: pointer;
  outline: none;
  box-shadow: 3px 3px 27px rgba(0, 0, 0, 0.10);
  color: #000;
  transition: 0.3s color, 0.3s background;

  .lg-min({
    width: 5000/1920vw;
    height: 5000/1920vw;
  });

  &.a svg{
    transform: translateY(5%);
  }

  &:hover{
    background: var(--color-main);
    color: #fff;

    &[data-count]::after{
      background: var(--color-2);
    }
  }

  &[data-count]::after{
    content: attr(data-count);
    width: 2000/50%;
    height: 2000/50%;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: -10%;
    right: -10%;
    background: var(--color-main);
    color: #fff;
    font-weight: 700;
    transition: 0.3s color, 0.3s background;

    .lg-min({
      font-size: 1200/1920vw;
    });
  }

  svg{
    position: relative;
    width: 50%;
    fill: currentColor;

    .lg-min({
      width: 2800/50%;
    });
  }
}


.social-link{
  position: relative;
  display: block;
  width: 3000/360vw;

  .lg-min({
    width: 2.1vw;
  });

  &[data-title]::after{
    content: '';
    display: block;
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    opacity: 0;
    top: 0;
    left: 0;
    transition: 0.3s opacity, 0.3s transform;
    background: var(--color-curr);
    pointer-events: none;
  }
  &[data-title="Telegram"]::after{
    --color-curr: rgba(54, 173, 225, 0.20);
  }
  &[data-title="WhatsApp"]::after{
    --color-curr: rgba(13, 193, 67, 0.20);
  }

  &:hover{
    &::after{
      opacity: 1;
      transform: scale(56/40);
    }
  }

  svg{
    display: block;
    position: relative;
    z-index: 1;
  }
}

.box-phones{
  position: relative;

  &__label{
    display: flex;
    align-items: center;
    padding: 0.6em 1.05em;
    border-radius: 1.5em;
    background: #F0EFF3;
    font-weight: 600;
    font-size: 1200/360vw;

    .lg-min({
      padding: 0.45em .75em;
      font-size: 0.94vw;
    });

    &::after{
      content: '';
      display: block;
      width: 10/18em;
      flex: 0 0 10/18em;
      height: 5/18em;
      margin-left: 1.2em;
      background: url(../img/svg/dropdown-1.svg) center / contain no-repeat;
    }

    i{
      display: block;
      width: 1.2em;
      flex: 0 0 1.2em;
      margin-right: 0.4em;
    }
  }
}

.cart-btn{
  display: flex;
  align-items: center;
  padding: .4em .4em .4em 1.1em;
  border-radius: 1.5em;
  font-size: 1.2em;
  font-weight: 700;
  box-shadow: 3px 3px 30px 0px rgba(0, 0, 0, 0.10);
  background: #fff;
  transition: 0.3s color, 0.3s background;

  .lg-min({
    height: 2.6vw;
    font-size: 0.94vw;
  });
  
  &:hover{
    background: var(--color-main);
    color: #fff;

    &>small{
      background: var(--color-2);
    }
  }

  &>i{
    display: block;
    width: 1.2em;
    margin-right: 0.8em;

    svg{
      fill: currentColor;
      stroke: currentColor;
    }
  }
  &>small{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 1.65em;
    width: 2em;
    height: 2em;
    background: var(--color-main);
    color: #fff;
    border-radius: 50%;
    font-weight: 600;
    font-size: 1em;
    transition: 0.3s color, 0.3s background;
  }
}

.profile-btn{
  position: relative;
  display: flex;
  align-items: center;
  padding: 0.4em 2.1em 0.4em 1.3em;
  border-radius: 1.5em;
  font-size: 1.2em;
  font-weight: 700;
  box-shadow: 3px 3px 30px 0px rgba(0, 0, 0, 0.10);
  background: #fff;
  transition: 0.3s color, 0.3s background;

  .lg-min({
    height: 2.6vw;
    font-size: 0.94vw;
  });

  &[data-status]::after{
    content: '';
    display: block;
    position: absolute;
    width: .95em;
    height: .95em;
    top: -5%;
    right: 0;
    margin-right: 8%;
    border-radius: 50%;
    background: var(--color-curr, transparent);
    transition: 0.2s background;
  }
  &[data-status="warn"]{
    --color-curr: var(--color-main);
  }
  
  &:hover{
    background: var(--color-main);
    color: #fff;

    &[data-status]::after{
      background: var(--color-2);
    }
  }

  &>i{
    display: block;
    width: 1.2em;
    margin-right: 0.8em;

    svg{
      fill: currentColor;
    }
  }
}


.cat{
  display: flex;
  align-items: center;
  justify-content: center;
  padding: .3em .8em .25em;
  width: 14500/360vw;
  height: 4000/360vw;
  border-radius: 2000/360vw;
  background: var(--color-main);
  border: 0;
  outline: none;
  color: #fff;
  font-weight: 700;
  font-size: 4.444445vw;
  transition: 0.3s background;

  .lg-min({
    width: 18400/1920vw;
    height: 5000/1920vw;
    font-size: 1.04vw;
  });

  &:hover{
    background: var(--color-2);
  }

  &>i{
    display: block;
    width: 1.2em;
    margin-right: .9em;

    svg{
      stroke: currentColor;
    }
  }
}

